<template>
  <div>
    <searchnav title="资讯"
               :show='false'></searchnav>
    <div v-html='news'
         class="news"></div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      news: ''
    }
  },
  mounted () {
    this.axios({
      url: '/zuo/gateway?actId=ElzMZU125260',
      headers: {
        "X-Host": "mall.act.static-page.info"
      }
    }).then(res => {
      console.log(res)
      this.news = res.data.data.data.content
    })
  }

}
</script>
<style lang="scss">
.news {
  width: 100vw;
  height: 100vh;
}
</style>